const Orchid = {
    /**
     * Creates a new controller class extending the base Controller.
     *
     * @param {Object} definition - The properties and methods of the controller.
     * @returns {Function} - A dynamically created controller class.
     */
    createController(definition) {
        return class extends window.Controller {
            constructor() {
                super();
                Object.assign(this, definition);
            }
        };
    },

    /**
     * Registers a controller with the application router.
     *
     * @param {string} name - The unique name of the controller.
     * @param {Object|Function} definition - The controller definition or constructor function.
     */
    register(name, definition) {
        if (window.application.router.modulesByIdentifier.has(name)) {
            console.warn(`Controller with name "${name}" already exists.`);
            return;
        }

        const controllerClass = typeof definition === 'function'
            ? this.createController(definition)
            : definition;

        window.application.register(name, this.createController(definition));
    }
};

export default Orchid;
